.footer__container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  max-width: 1148px;
}

.footer {
  width: 100%;
  border-top: 1px solid rgba(72, 72, 72, 0.2);
  padding-top: 23px;
  padding-bottom: 24px;
}

.footer__icon {
  fill: rgba(72, 72, 72, 0.2);
}

.footer__text {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 150%;
  color: rgba(72, 72, 72, 0.5);
  margin-top: 6px;
  margin-left: 32px;
}

.footer__social {
  margin-right: -12px;
}

.footer__link {
  margin-right: 17px;
}

@media (max-width: 1170px) {
  .footer__container {
    justify-content: space-around;
  }
}

@media (max-width: $tablet-width) {
  .footer__container {
    justify-content: space-around;
  }
}

@media (max-width: $mobile-width) {
  .footer__container {
    flex-direction: column;
    align-items: center;
  }

  .footer__logo {
    margin-bottom: 15px;
  }

  .footer__social {
    margin-top: 32px;
    margin-left: -5px;
  }

  .footer__icon {
    width: 30px;
    height: 30px;
  }

  .footer__text {
    margin-left: -3px;
  }

  .footer {
    padding-top: 27px;
  }
}
